<script>
export default {
  name: 'texto',
  data () {
    return {
      msg: "Made with a lot of love so you won't have to worry about surviving in Japan anymore with minimal skills in the Japanese language. Sit back, relax, and enjoy your trip.",
      msg2: 'Click the titles to toggle!',
      msg3: 'おはよう => "Ohayou"',
      img: ['http://res.cloudinary.com/takeout/image/upload/v1491482348/738939_g7x7wk.png', 'http://res.cloudinary.com/takeout/image/upload/v1491485274/737686_dvrjjp.png'],
      visible1: false,
      visible2: false,
      visible3: false
    }
  },
  methods: {
    toggle1: function () {
      this.visible1 = !this.visible1
    },
    toggle2: function () {
      this.visible2 = !this.visible2
    },
    toggle3: function () {
      this.visible3 = !this.visible3
    }
  },
  components: {
    'greet': {
      template: ''
    }
  }
}
