<template>
<div class="carousel" v-on:mouseover="pauseTimer" v-on:mouseout="setTimer">
    <slot></slot>
    <button class="carousel__nav carousel__next" @click.prevent="next"><i class="fa fa-chevron-right" aria-hidden="true"></i></button>
    <button class="carousel__nav carousel__prev" @click.prevent="prev"><i class="fa fa-chevron-left" aria-hidden="true"></i></button>
    <div class="carousel__pagination">
          <button v-for="n in slidesCount" @click="goto(n-1)" :class="{active: n-1 == index}"></button>
    </div>
</div>
</template>

<script>
export default {
  data () {
    return {
      index: 0,
      slides: [],
      direction: null
    }
  },
  mounted () {
    this.slides = this.$children
    this.slides.forEach((slide, i) => {
      slide.index = i
    })
  },
  computed: {
    slidesCount () { return this.slides.length }
  },
  methods: {
    setTimer () {
      this.timer = setInterval(() => {
        this.index++
        this.direction = 'right'
        if (this.index >= this.slidesCount) {
          this.index = 0
        }
      }, 3000
    )
    },
    pauseTimer () {
      clearInterval(this.timer)
      setTimeout(this.timer, 4000)
    },
    next () {
      this.index++
      this.direction = 'right'
      if (this.index >= this.slidesCount) {
        this.index = 0
      }
    },
    prev () {
      this.index--
      this.direction = 'left'
      if (this.index < 0) {
        this.index = this.slidesCount - 1
      }
    },
    goto (index) {
      this.direction = index > this.index ? 'right' : 'left'
      this.index = index
    }
  }
}

</script>

<style src="./style.css">
</style>




